import { Mode } from '../models/clock';
import { Button } from '../components/button';
var ClockController = /** @class */ (function () {
    function ClockController(clock, view) {
        this.clock = clock;
        this.view = view;
        this.init();
    }
    ClockController.prototype.init = function () {
        this.view.render(this.clock);
        var modeButton = new Button('Mode', this.handleModeButtonClick.bind(this));
        var increaseButton = new Button('Increase', this.handleIncreaseButtonClick.bind(this));
        var lightButton = new Button('Light', this.handleLightButtonClick.bind(this));
        modeButton.render();
        increaseButton.render();
        lightButton.render();
    };
    ClockController.prototype.handleModeButtonClick = function () {
        var mode = this.clock.getMode();
        if (mode === Mode.DISPLAY) {
            this.clock.setMode(Mode.EDIT_HOUR);
        }
        else if (mode === Mode.EDIT_HOUR) {
            this.clock.setMode(Mode.EDIT_MINUTE);
        }
        else {
            this.clock.setMode(Mode.DISPLAY);
        }
        this.view.updateTimeDisplay(this.clock.getCurrentTime());
    };
    ClockController.prototype.handleIncreaseButtonClick = function () {
        var mode = this.clock.getMode();
        if (mode === Mode.EDIT_HOUR) {
            this.clock.incrementHour();
        }
        else if (mode === Mode.EDIT_MINUTE) {
            this.clock.incrementMinute();
        }
        this.view.updateTimeDisplay(this.clock.getCurrentTime());
    };
    ClockController.prototype.handleLightButtonClick = function () {
        document.body.style.backgroundColor === 'yellow' ?
            this.view.toggleLight(false) : this.view.toggleLight(true);
    };
    return ClockController;
}());
export { ClockController };
