var ClockView = /** @class */ (function () {
    function ClockView() {
    }
    ClockView.prototype.render = function (clock) {
        var timeElement = document.getElementById('time');
        timeElement.innerText = clock.getCurrentTime().toLocaleTimeString();
    };
    ClockView.prototype.updateTimeDisplay = function (time) {
        var timeElement = document.getElementById('time');
        timeElement.innerText = time.toLocaleTimeString();
    };
    ClockView.prototype.toggleLight = function (on) {
        var bodyElement = document.body;
        bodyElement.style.backgroundColor = on ? 'yellow' : 'white';
    };
    return ClockView;
}());
export { ClockView };
